<template>
  <div class="photos-comp">

    <!-- Ð¡ÑÑÐ¾ÐºÐ° Ð¿Ð¾Ð¸ÑÐºÐ° -->
    <v-layout
      class="search-container"
      justify-center=""
      align-center=""
      >
      <!-- <v-spacer></v-spacer> -->
      <v-flex md6>
        <v-text-field
          @keyup="searchClear"
          @keyup.enter="photoSearch"
          v-model="inputSearch"

          class="mx-3 mt-3 search"
          flat
          label="Search Photos"
          color = "#218ccc"
          single-line
          outline
        ></v-text-field>
        <div
          class="search-icon-layout"
          @click="photoSearch"
        >
          <v-icon>search</v-icon>
        </div>
      </v-flex>

      <!-- <v-spacer></v-spacer> -->

      <div class="sort-photo">
        <v-menu
          transition="scale-transition"
          origin="top center"
          offset-y
          allow-overflow
          open-on-hover
          close-delay=500
        >
          <template v-slot:activator="{ on }">
            <v-btn
              class=" pl-4"
              outline
              color="#218ccc"
              v-on="on"
              @click="sortByPhoto"
            >
              {{ sortTitle.toUpperCase() }} <v-icon
              class=" ml-1"
              :style="arrowReverse()"
              >keyboard_arrow_down</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-tile
              v-for="(item, index) in filters"
              :key="index"
              @click="sortTitle=item.title, sortByPhoto(item.title)"

            >
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
      </div>

    </v-layout>

    <!-- Ð¤Ð¾ÑÐ¾-Ð³Ð°Ð»Ð»ÐµÑÐµÑ -->
    <v-container
      class="photo-gallery"
      fluid grid-list-sm
    >
      <v-layout row wrap>
        <v-flex
          v-show="photo.isShow"

          v-resize="onResize"
          v-for="(photo,i) in photoAlbum" :key="i"
          class="photo__item " :class="phGrid"
        >
          <v-hover
            open-delay=300
          >
            <v-img
              slot-scope="{ hover }"
              :src=photo.url
              class="image"
              :height="phHeight"
              @click.stop="dialogPhoto = true; photoItem=photo"
            >
            <!-- ÐÑÐ¿Ð»ÑÐ²Ð°ÑÑÐ¸Ðµ ÑÐ»ÐµÐ¼ÐµÐ½ÑÑ Ð²Ð½ÑÑÑÐ¸ ÑÐ¾ÑÐ¾Ð³ÑÐ°ÑÐ¸Ð¸ -->
              <v-expand-transition>
                <div
                  v-if="hover"
                  class="transition-linear-out-fast-in white--text
                        bt-sheets"
                  style="height: auto;"
                >
                  <div>
                    <div class="item-title">{{photo.title}}</div>
                    <div class=" mb-2">
                      <div class="item-votes"><v-icon color="white">how_to_vote</v-icon> {{photo.vote}}</div>
                      <div class="item-views"><v-icon color="white">visibility</v-icon> {{photo.view}}</div>
                    </div>
                  </div>
                </div>
              </v-expand-transition>

              <v-expand-transition>
                <div
                  v-if="hover"
                  class="achiev display-1 d-flex"
                >
                  <v-img
                    :src=photo.achiev.url
                    max-width=130
                  ></v-img>
                </div>
              </v-expand-transition>
            </v-img>
          </v-hover>

          <!-- ÐÑÐ¾ÑÐ¼Ð¾ÑÑ ÑÐ¾ÑÐ¾ Ð²Ð¾ Ð²ÐµÑÑ ÑÐºÑÐ°Ð½ -->
          <v-dialog
            v-model="dialogPhoto"
            fullscreen
            dark
            persistent
          >
            <v-card class="">
              <v-img class="photo-view"
                contain
                max-height='100vh'
                :src=photoItem.url
              >
              <!-- Ð­Ð»ÐµÐ¼ÐµÐ½ÑÑ Ð½Ð°Ð²Ð¸Ð³Ð°ÑÐ¸Ð¸ Ð² Ð¿ÑÐ¾ÑÐ¼Ð¾ÑÐ¾ÑÑÐ¸ÐºÐµ -->
                <v-icon
                  class="close"
                  large
                  @click="dialogPhoto=false"
                >clear</v-icon>

                <div class="btn-arrow--h" :class="classBtnArrow" >
                  <v-icon
                    size=40
                    class="arrow_back hide"
                    @click="backPhoto()"
                  >arrow_back_ios
                  </v-icon>

                  <v-icon
                    size=40
                    class="arrow_next hide"
                    @click="nextPhoto()"
                  >arrow_forward_ios
                  </v-icon>
                </div>
              </v-img>
            </v-card>
          </v-dialog>

        </v-flex>
      </v-layout>
    </v-container>

  </div>
</template>

<script>
let achievs = {
  top10: {
          url: require('../assets/image/achievement/top-10_white.svg'),
          power: 10
          },
  top20: {
          url: require('../assets/image/achievement/top-20_white.svg'),
          power: 20
          },
  top30: {
          url: require('../assets/image/achievement/top-30_white.svg'),
          power: 30
          },
  top100: {
          url: require('../assets/image/achievement/top-100_white.svg'),
          power: 100
            },
  guru_pick: {
          url: require('../assets/image/achievement/gurus-pick_white.svg'),
          power:300
          }
}

export default {
  data() {
    return {
      dialogPhoto: false,

      phHeight:300,
      phGrid:'',
      classBtnArrow: '',

      inputSearch:'',
      sortTitle:'SORT BY',
      rever: true, //ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ðµ ÑÑÑÐµÐ»ÐºÐ¸ Ð² ÐºÐ¾Ð½Ð¾Ð¿ÐºÐµ ÑÐµÐ²ÐµÑÑÐ¸Ð²Ð½Ð¾Ð¹ ÑÐ¾ÑÑÐ¸ÑÐ¾Ð²ÐºÐ¸

      photoItem:'',
      photoAlbum:[
        {
          title:'The ghost of war, of fascism, of total evil. Fire demon.',
          url:require('../assets/image/photo_album/pic_1.jpg'),
          vote: 2342,
          view: 1220,
          achiev: achievs.top10,
          tags:'',
          date:'2018-05-09',
          isShow: true,
        },
        {
          title:'Flight through the storm.',
          url:require('../assets/image/photo_album/pic_2.jpg'),
          vote: 1909,
          view: 1100,
          achiev: achievs.top20,
          tags:'',
          date:'2015-05-28',
          isShow: true,
        },
        {
          title:'Happy young grandmother with granddaughter.',
          url:require('../assets/image/photo_album/pic_3.jpg'),
          vote: 1602,
          view: 1676,
          achiev: achievs.top100,
          tags:'',
          date:'2018-10-05',
          isShow: true,
        },
        {
          title:'',
          url:require('../assets/image/photo_album/pic_4.jpg'),
          vote: 1262,
          view: 863,
          achiev: achievs.top30,
          tags:'flowers',
          date:'2018-06-20',
          isShow: true,
        },
        {
          title:'Snowflake flower in late autumn.',
          url:require('../assets/image/photo_album/pic_5.jpg'),
          vote: 1239,
          view: 948,
          achiev: achievs.top30,
          tags:'flowers',
          date:'2018-09-25',
          isShow: true,
        },
        {
          title:'',
          url:require('../assets/image/photo_album/pic_6.jpg'),
          vote: 2926,
          view: 2641,
          achiev: achievs.guru_pick,
          tags:'',
          date:'2018-09-29',
          isShow: true,
        },
      ],
      filters: [
        { title: 'Votes'},
        { title: 'Views'},
        { title: 'Achievements' },
        { title: 'Newest' },
      ]
    }
  },
  mounted () {
    this.onResize(),
    this.sortByPhoto('Newest')
  },
  methods: {

    // Ð½Ð°Ð²Ð¸Ð³Ð°ÑÐ¸Ñ Ð² Ð¾ÐºÐ½Ðµ Ð¿ÑÐ¾ÑÐ¼Ð¾ÑÑÐ°
    nextPhoto() {
      let photo = this.photoItem;
      let index = this.photoAlbum.indexOf(photo);

      index++;

      if (index < this.photoAlbum.length) {
        while (!this.photoAlbum[index].isShow) {
          index++;
          if (index == this.photoAlbum.length) index = 0;
        }

        this.photoItem = this.photoAlbum[index];
      } else {
        index = 0;
        this.photoItem = this.photoAlbum[index];
      }
    },
    backPhoto() {
      let photo = this.photoItem;
      let index = this.photoAlbum.indexOf(photo);

      index--;
      if (index >= 0 ) {
        while (!this.photoAlbum[index].isShow) {
          index--;
          if (index < 0) index = this.photoAlbum.length-1;
        }

        this.photoItem = this.photoAlbum[index];
      } else {
        index = this.photoAlbum.length-1;
        this.photoItem = this.photoAlbum[index];
      }
    },

    // ÑÑÐ½ÐºÑÐ¸Ñ, Ð¾ÑÑÐ»ÐµÐ¶Ð¸Ð²Ð°ÑÑÐ°Ñ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ðµ ÑÐ¸ÑÐ¸Ð½Ñ, Ð²ÑÑÐ¾ÑÑ Ð¾ÐºÐ½Ð°, Ð´ÐµÐ»Ð°ÑÑÐ°Ñ Ð¿Ð¾Ð¿ÑÐ°Ð²ÐºÐ¸ Ð² ÐºÐ¾Ð´Ðµ
    onResize() {
      // Ð°Ð´Ð°Ð¿ÑÐ°ÑÐ¸Ñ Ð²ÑÑÐ¾ÑÑ Ð¸ ÑÐ°ÑÐ¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ñ ÑÐ¾ÑÐ¾ Ð² Ð°Ð»ÑÐ±Ð¾Ð¼Ðµ
      let screen = document.body.clientWidth;
      if(screen<490) {
        this.phHeight=240;
      } else if (screen>=490 && screen<720) {
        this.phGrid = 'xs12';
        this.phHeight=300;
      } else if (screen>=720 && screen<960) {
        this.phGrid = 'xs6';
        this.phHeight=240;
      } else if(screen>=960) {
        this.phGrid = 'xs6';
        this.phHeight=300;
      }

      // Ð°Ð´Ð°Ð¿ÑÐ°ÑÐ¸Ñ Ð·Ð¾Ð½Ñ ÑÐ°ÑÐ¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ñ ÑÑÑÐµÐ»Ð¾Ðº Ð½Ð°Ð²Ð¸Ð³Ð°ÑÐ¸Ð¸ Ð² Ð¾ÐºÐ½Ðµ Ð¿ÑÐ¾ÑÐ¼Ð¾ÑÑÐ°
      let pvHeight = document.documentElement.clientHeight;
      let pvWidth = document.documentElement.clientWidth;
      if ((pvHeight*3/2)<=pvWidth) {
        this.classBtnArrow = '';
      } else {
        this.classBtnArrow = 'btn-arrow--v';
      }
    },

    //Ð¾ÑÐ¸ÑÑÐºÐ° Ð¿Ð¾Ð¸ÑÐºÐ° Ð¿ÑÐ¸ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ð¸ ÑÑÑÐ¾ÐºÐ¸ Ð¸Ð· input
    searchClear() {
      let str = this.inputSearch;
      if (str=='') {
        this.photoAlbum.forEach(photo => {
          photo.isShow=true;
        });
      }
    },

    // Ð¿Ð¾Ð¸ÑÐº ÑÐ¾ÑÐ¾
    photoSearch() {
      let str = this.inputSearch;
      let album = this.photoAlbum;
      // console.log(str);

      album.forEach(photo => {
        if (str!='') {
            let tags = photo.tags;

            if(tags.indexOf(str.toLowerCase())!=-1) photo.isShow=true;
            else photo.isShow=false;
        }
      });
    },

    // ÑÐ¾ÑÑÐ¸ÑÐ¾Ð²ÐºÐ° ÑÐ¾ÑÐ¾
    sortByPhoto(filter) {
      let album = this.photoAlbum;

      switch (filter) {
        case 'Votes':
          votes(album);
          this.rever = true;
          break;
        case 'Views':
          views(album);
          this.rever = true;
          break;
        case 'Achievements':
          achiev(album);
          this.rever = true;
          break;
        case 'Newest':
          newest(album);
          this.rever = true;
          break;

        default:
          console.log('Album reverse');
          this.rever = !(this.rever);
          return album.reverse();
          break;
      }

      function votes(album) {
        console.log('Votes');
        
        album.sort((a,b)=>{
          if(a.vote<b.vote) return 1;
          if(a.vote>b.vote) return -1
        });
        return album;
      };

      function views(album) {
        console.log('Views');
        
        album.sort((a,b)=>{
          if(a.view<b.view) return 1;
          if(a.view>b.view) return -1
        });
        return album;
      };

      function achiev(album) {
        console.log('Achievements');

         album.sort((a,b)=>{
          if(a.achiev.power<b.achiev.power) return 1;
          if(a.achiev.power>b.achiev.power) return -1
        });
        return album;
      };

      function newest(album) {
        console.log('Newest');

        album.sort((a,b)=>{
          if(a.date<b.date) return 1;
          if(a.date>b.date) return -1
        });
        return album;
      };
    },
    // ÑÐ°Ð·Ð²Ð¾ÑÐ¾Ñ ÑÑÑÐµÐ»ÐºÐ¸ Ð² ÐºÐ½Ð¾Ð¿ÐºÐµ Ð¿ÑÐ¸ ÑÐµÐ²ÐµÑÑÐ¸Ð¾Ð½Ð½Ð¾Ð¹ ÑÐ¾ÑÑÐ¸ÑÐ¾Ð²ÐºÐµ
    arrowReverse() {
      if(this.rever) {
        return "transform: scaleY(1)"
      } else {
        return "transform: scaleY(-1);"
      }
    },
  }
}
</script>

<style>
@import '../assets/css/home_component/photos.css';
</style>



